<template>
    <div id="app">
        <div class="container">
            <button @click="GetTargetImages()" :class="{'pure-button':true, 'pure-button-disabled': isCompiling}" id="refreshBtn" >Refresh data</button>
            <div class="tableDiv" 
                 v-for="(sceneData, sceneIdx, index) in scenesTargetsData"
                       :key="index">
                <table>
                    <tr class="tableHeaderRow">
                        <td colspan="3"
                            class="tableHeader">
                            Scene index: {{sceneIdx}}
                        </td>
                    </tr>
                    <tr class="headerRow">
                        <td>Idx</td>
                        <td>Target image name</td>
                        <td>Image preview</td>
                    </tr>
                    <tr class="dataRow"
                        v-for="(target, targetIdx) in sceneData"
                        :key="targetIdx">
                        <td>{{targetIdx}}</td>
                        <td>{{target.name}}</td>
                        <td class="cDataImg"><img :src="target.src" alt="Alternate Text" /></td>
                    </tr>
                    <tr class="compileBtnRow">
                        <td colspan="3">
                            <button :class="{'pure-button':true, 'pure-button-disabled': isCompiling}"
                                    id="compileBtn" 
                                    @click="Compile(sceneIdx, sceneData)">Compile .mind file for scene {{sceneIdx}}</button>
                            <label v-if="scenesInfo[sceneIdx]">{{scenesInfo[sceneIdx]}}</label>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import "mind-ar/dist/mindar-image.prod.js";
    import axios from "axios";

    export default {
        name: 'App',
        data() {
            return {
                scenesTargetsData: {},
                scenesInfo: {},
                isCompiling: false,
                compiler: null
            };
        },
        mounted() {
            this.GetTargetImages();
            this.compiler = new window.MINDAR.IMAGE.Compiler();
        },
        methods: {

            GetTargetImages() {
                const vue = this;
                axios.get('/gettargets')
                    .then((response) => {
                        vue.ParseData(response.data);
                    })
                    .catch((error) => {
                        console.warn(error);
                    })
            },

            async ParseData(data) {
                let scenesTargetsData = {}
                for (const [, schemaData] of Object.entries(data)) {
                    for (const [, targetData] of Object.entries(schemaData)) {
                        if (!scenesTargetsData[targetData.sceneIndex]) {
                            scenesTargetsData[targetData.sceneIndex] = new Array(Object.keys(schemaData).length);
                        }
                        let img = await this.LoadImage(targetData.targetImgName + ".png");
                        scenesTargetsData[targetData.sceneIndex][targetData.objectIndex] = img;
                    } 
                }  
                this.scenesTargetsData = scenesTargetsData;
            },

            async Compile(sceneIdx, data) {
                if (data.length == 0) {
                    alert("Unable to find correct images. Only .png and .jpg images are supported.");
                    return;
                }
                let dotMindFile = await this.GenerateTarget(sceneIdx, data);
                this.SendFile(dotMindFile);
            },

            async GenerateTarget(sceneIdx, files) {
                const vue = this;
                vue.isCompiling = true;

                let dotMindData = new Array();
                for (var i = 0; i < files.length;) {
                    const dataList = await this.compiler.compileImageTargets([files[i]], (progress) => {
                        let num = Math.round(Number(progress));
                        if (num == 100 && i == files.length-1) {
                            vue.scenesInfo[sceneIdx] = `Done!`;
                            vue.isCompiling = false;
                        } else {
                            vue.scenesInfo[sceneIdx] = `Progress: ${i+1}/${files.length} ${num}%`;
                        }
                    });
                    i++;

                    dotMindData.push(dataList[0]);
                }

                this.compiler.data = dotMindData                
                const buffer = await this.compiler.exportData();
                let file = this.BufferToFile(sceneIdx, buffer);
                return file;
            },

            async LoadImage(file) {
                const name = file;
                return new Promise((resolve, reject) => {
                    let img = new Image();
                    img.onload = () => resolve(img);
                    img.name = name;
                    img.onerror = () => {
                        reject();
                        throw new Error("Something went wrong with " + file);
                    };
                    img.src = file;
                });
            },

            BufferToFile(sceneIdx, buffer) {
                var blob = new Blob([buffer]);
                var file = new File([blob], `targets_${sceneIdx}.mind`);
                return file;
            },

            SendFile(file) {
                const form = new FormData();
                form.append('dotMindFile', file);

                axios.post('/dotmind', form);
            },
        }
    }
</script>

<style>
    @import '../css/styles.css';
    .container {
        max-width: 800px;
        min-width:500px;
        margin: auto;
    }

    .tableDiv{
        width: 100%;
        margin-bottom: 30px;
        padding: 0px;
        border:1px solid lightgray;
        border-radius: 5px;
    }

    table {
        padding: 0px;
        width: 100%;
        border-collapse: collapse;
        border-spacing:0;
    }

        table tr:nth-child(1)  {
            background-color:lightgray;
        }

            table tr:nth-child(1) td {
                font-size: 16px;
            }

        table tr:not(:first-child) {
            border-top: 1px solid lightgray;
        }

        table tr:last-child td {
            padding: 0;
        }



        table td:nth-child(1) {
            padding-left: 10px;
            padding-right: 10px;
            padding-top: 5px;
            padding-bottom: 5px;
            text-align:center;
        }

        table td:nth-child(3) {
            text-align: center;
        }

    .cDataImg {
        padding: 5px;
        height: 100px;
        width: 100px;
        object-fit: contain;
    }
    #compileBtn {
        width: 100%;
        height: 30px;
        background: #1cb841;
        color: white;
    }

    #refreshBtn {
        width: 100%;
        height:30px;
        background: #1cb841;
        color: white;
        margin-top: 10px;
        margin-bottom: 10px;
    }
</style>
